<template>
  <div class="isseen">
      <h1  class="large_t">我看过的</h1>
      <div class="today" v-for="item in list" :key="item">
          <div class="top">
              <p class="clearfix time">
                <span class="fl three_t_m">{{item}}</span>
                <span class="fr three_t_m">删除</span>
              </p>
              <ul class="clearfix">
                  <li class="fl" v-for="i in 3" :key="i">
                      <img src="../../assets/test/goods.png" alt="">
                      <p class="three_t_h">尊享礼盒</p>
                  </li>
              </ul>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  name: "Isseen",
  data() {
    return {
        list: [
            '今日',
            '2017-11-11'
        ]
    };
  },
  methods: {},
  components: {}
};
</script>

<style scoped lang="less">
.isseen {
  background-color: #f2f2f2;
  font-size: 0.16rem;
  h1 {
    padding: 0.3rem 0.27rem 0 0.28rem;
    background-color: #fff;
  }
  .today {
    background-color: #fff;
    margin-bottom: 0.1rem;
    .top {
      padding: 0 0.27rem 0 0.28rem;
      .time {
        padding: 0.15rem 0rem 0.05rem 0rem;
        border-bottom: 0.01rem solid #f2f2f2;
      }
      ul {
        li {
          padding-top: 0.15rem;
          padding-bottom: 0.15rem;
          &:nth-child(3n + 2) {
            padding-left: 0.1rem;
            padding-right: 0.1rem;
          }
          img {
            display: block;
            width: 1rem;
            height: 1rem;
            border-radius: 0.12rem;
          }
        }
      }
    }
  }
}
</style>



